import { HTTP } from "@/main";

export const RatingsMail = payload => {
  return HTTP.post("/General/Sendmail", {
    From: "InfraSelfService@tfg.co.za",
    Body: `Rating: ${payload.rating}
Comment: ${payload.comment}`,
    To: "jeansn@tfg.co.za",
    Smtp: "smtp.tfg.co.za",
    Subject: "Infrastructure Self Service Feedback"
  });
};
