<template>
  <div>
    <v-btn
      color="secondary"
      dark
      small
      absolute
      top
      round
      right
      class="Feedback"
      @click="dialog=!dialog"
    >Feedback</v-btn>
    <v-dialog v-model="dialog" max-width="400px">
      <v-card class="elevation-16 mx-auto" width="400" light>
        <v-card-title class="headline" primary-title>Rate this Service</v-card-title>
        <v-card-text>
          If you enjoy using the Infrastructure Self Service, please take a few seconds to rate
          your experience. It really helps!
          <div class="text-xs-center mt-3">
            <v-rating
              v-model="rating"
              color="yellow darken-3"
              background-color="grey darken-1"
              empty-icon="$vuetify.icons.ratingFull"
              half-increments
              large
              hover
            ></v-rating>
          </div>
        </v-card-text>
        <v-card-text>
          <v-textarea name="input-7-4" label="Comments?" v-model="comment"></v-textarea>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="justify-space-between">
          <v-btn @click="dialog=false">No Thanks</v-btn>
          <v-btn color="secondary" @click="Mail()">Submit</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { RatingsMail } from "@/services/api/General";
import { Notify } from "@/services/HelperService";

export default {
  data() {
    return {
      loading: false,
      dialog: false,
      rating: 0,
      comment: ""
    };
  },
  methods: {
    Mail() {
      RatingsMail({ comment: this.comment, rating: this.rating })
        .then(() => {
          this.$notify({
            group: "notify",
            type: "success",
            title: "Thank you for your feedback!"
          });
          this.dialog = false;
        })
        .catch(error => {
          this.$notify(Notify(error));
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.Feedback {
  animation: shake 1.5s linear 0s alternate infinite none running;
  margin-top: 30px !important;
}

@keyframes shake {
  0% {
    transform: rotate(3deg);
  }
  100% {
    transform: rotate(-3deg);
  }
}
</style>

