import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "home",
      component: Home
    },
    {
      path: "/ActiveDirectory/Users/FindUser",
      name: "FindUser",
      component: () => import("./views/ActiveDirectory/Users/FindUser.vue")
    },
    {
      path: "/ActiveDirectory/Groups/FindGroup",
      name: "FindGroup",
      component: () => import("./views/ActiveDirectory/Groups/FindGroup.vue")
    },
    {
      path: "/ActiveDirectory/Computers/FindComputers",
      name: "FindComputers",
      component: () =>
        import("./views/ActiveDirectory/Computers/FindComputers.vue")
    },
    {
      path: "/ActiveDirectory/Track/TrackChanges",
      name: "TrackChanges",
      component: () => import("./views/ActiveDirectory/Track/TrackChanges.vue")
    },
    {
      path: "/BiztalkServices/Biz2016TSTFeeds",
      name: "Biz2016TSTFeeds",
      component: () => import("./views/BiztalkServices/Biz2016TSTFeeds.vue")
    },
    {
      path: "/BiztalkServices/Biz2013TSTFeeds",
      name: "Biz2013TSTFeeds",
      component: () => import("./views/BiztalkServices/Biz2013TSTFeeds.vue")
    },
    {
      path: "/VirtualMachines/Eventlogs",
      name: "Eventlogs",
      component: () => import("./views/VirtualMachines/Eventlogs.vue")
    },
    {
      path: "/VirtualMachines/ServerInfo",
      name: "ServerInfo",
      component: () => import("./views/VirtualMachines/ServerInfo.vue")
    },
    {
      path: "/SQLServer/RestoreRequest",
      name: "RestoreRequest",
      component: () => import("./views/SQLServer/RestoreRequest.vue")
    },
    {
      path: "/SQLServer/RestoresPending",
      name: "RestoresPending",
      component: () => import("./views/SQLServer/RestoresPending.vue")
    },
    {
      path: "/SQLServer/RestoresCompleted",
      name: "RestoresCompleted",
      component: () => import("./views/SQLServer/RestoresCompleted.vue")
    },
    {
      path: "*",
      redirect: "/"
    }
  ]
});
