<template>
  <div>
    <!-- Top Nav bar Starts-->
    <v-toolbar app dense fixed clipped-left dark position="relative" color="tfgprimary">
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title>
        <span style="cursor: pointer">Infrastructure Self Service</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <!-- <ratings></ratings> -->
      <ratings></ratings>
    </v-toolbar>

    <!-- Top Nav bar Ends-->
    <v-navigation-drawer
      app
      light
      fixed
      clipped
      disable-route-watcher
      :temporary="isMobile"
      :stateless="dialog"
      v-model="drawer"
      @click.stop="clicked"
    >
      <v-layout justify-space-between column fill-height>
        <v-layout>
          <v-flex>
            <v-list dense class="mt-2">
              <v-list-tile router to="/">
                <v-list-tile-action>
                  <v-icon>home</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>Home</v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>

              <div v-for="(MenuItem, i) in MenuItems" :key="i">
                <v-list-group :prepend-icon="MenuItem.icon">
                  <v-list-tile slot="activator">
                    <v-list-tile-title>{{ MenuItem.title }}</v-list-tile-title>
                  </v-list-tile>

                  <div v-for="(subMenuItem, i) in MenuItem.subMenuItems.items" :key="i">
                    <v-list-group no-action sub-group>
                      <v-list-tile slot="activator">
                        <v-list-tile-title>{{ subMenuItem.title }}</v-list-tile-title>
                      </v-list-tile>

                      <v-list-tile
                        v-for="(item, i) in subMenuItem.items"
                        :key="i"
                        router
                        :to="item[2]"
                      >
                        <v-list-tile-title v-text="item[0]"></v-list-tile-title>
                        <v-list-tile-action>
                          <v-icon v-text="item[1]"></v-icon>
                        </v-list-tile-action>
                      </v-list-tile>
                    </v-list-group>
                  </div>
                </v-list-group>
              </div>
            </v-list>
          </v-flex>
        </v-layout>
        <v-layout shrink grow column align-center>
          <v-flex align-self-center>
            <p class="mb-4 body-1">UX/UI Designed by Jean Snyman</p>
          </v-flex>
        </v-layout>
      </v-layout>
    </v-navigation-drawer>
  </div>
</template>

