          subMenuItems: {
            items: [
              {
                title: "Feeds UAT",
                items: [
                   ["2016 Feeds", "list", "/BiztalkServices/Biz2016TSTFeeds"], 
                  ["2013 Feeds", "list", "/BiztalkServices/Biz2013TSTFeeds"]
                ]
              }
            ]
          }
        }, */
        /* {
          icon: "computer",
          title: "Compute Servers",
          subMenuItems: {
            items: [
              {
                title: "Windows",
                items: [
                  ["Find Events", "filter_list", "/VirtualMachines/Eventlogs"], 
                  ["Server Info", "info", "/VirtualMachines/ServerInfo"]
                ]
              }
            ]
          }
          
        }*/
      ]
    };
