export const Notify = error => {
  if (error.response === undefined) {
    return {
      group: "notify",
      type: "error",
      text: "Oops something went wrong...we are investigating."
    };
  }

  let {
    status,
    statusText,
    data
  } = error.response;

  if (status === 403) {
    return {
      group: "notify",
      type: "error",
      title: statusText,
      text: "Your account do not have permission to perform this request."
    };
  } else if (status === 404) {
    return {
      group: "notify",
      type: "error",
      title: statusText,
      text: "Oops we couldn't find this resource...we are investigating."
    };
  } else if (status === 401) {
    return {
      group: "notify",
      type: "error",
      title: statusText,
      text: "Access denied."
    };
  } else if (status === 400) {
    return {
      group: "notify",
      type: "error",
      title: statusText,
      text: data
    };
  } else {
    return {
      group: "notify",
      type: "error",
      title: statusText,
      text: "Oops something went wrong...we are investigating."
    };
  }
};